import request from '@/utils/request'

export function login(username, password) {
  return request({
    url: '/auth',
    method: 'post',
    // params: { username, password }
    data: {
      username,
      password
    }
  })
}

export function getInfo(token) {
  return request({
    url: '/user/info',
    method: 'get'
    // headers: { 'Authorization': token }
  })
}

export function logout() {
  return request({
    url: '/user/logout',
    method: 'post'
  })
}
