import Mock from 'mockjs'

const List = []
const count = 20

for (let i = 0; i < count; i++) {
  List.push(Mock.mock({
    id: '@id',
    title: '@title(1, 2)',
    'status|1': ['draft', 'deleted', 'published'],
    author: '@name()',
    display_time: '@datetime',
    pageviews: '@integer(60, 100)'
  }))
}

export default {
  getTableList: () => {
    // return tableMap['test']
    return {
      status: 'success',
      data: {
        items: List
      }
    }
  }
}
