import Mock from 'mockjs'
import tableAPI from './table'
import userAPI from './user'
import orgAPI from './org'

// 修复在使用 MockJS 情况下，设置 withCredentials = true，且未被拦截的跨域请求丢失 Cookies 的问题
// https://github.com/nuysoft/Mock/issues/300
Mock.XHR.prototype.proxy_send = Mock.XHR.prototype.send
Mock.XHR.prototype.send = function() {
  if (this.custom.xhr) {
    this.custom.xhr.withCredentials = this.withCredentials || false
  }
  this.proxy_send(...arguments)
}

// Mock.setup({
//   timeout: '350-600'
// })

// 组织相关
Mock.mock(/\/org\/tree/, 'get', orgAPI.getTreeData)
Mock.mock(/\/org\/list/, 'get', orgAPI.getTableList)

// 测试列表
Mock.mock(/\/table\/list/, 'get', tableAPI.getTableList)

// 用户相关
Mock.mock(/\/auth/, 'post', userAPI.auth)
Mock.mock(/\/user\/info/, 'get', userAPI.login)
Mock.mock(/\/user\/list/, 'get', userAPI.getList)
Mock.mock(/\/user\/detail/, 'get', userAPI.getuser)
Mock.mock(/\/user\/create/, 'post', userAPI.createUser)
Mock.mock(/\/user\/update/, 'post', userAPI.updateUser)

export default Mock
